.continue-btn {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.continue-btn button {
  border: none;
  padding: 10px 50px;
  background: #050f56;
  border: 1px solid #c4c4c4;
  border-radius: 10px;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 20px;
  font-family: "Roboto";
}
